export function deepClone<T>(obj: T): T {
	if (!obj || typeof obj !== 'object') {
		return obj;
	}
	if (obj instanceof RegExp) {
		// See https://github.com/microsoft/TypeScript/issues/10990
		return obj as any;
	}
	const result: any = Array.isArray(obj) ? [] : {};
	Object.keys(<any>obj).forEach((key: string) => {
		if ((<any>obj)[key] && typeof (<any>obj)[key] === 'object') {
			result[key] = deepClone((<any>obj)[key]);
		} else {
			result[key] = (<any>obj)[key];
		}
	});
	return result;
}

// from https://github.com/microsoft/vscode/blob/43ae27a30e7b5e8711bf6b218ee39872ed2b8ef6/src/vs/base/common/objects.ts#L117
export function objectEquals(one: any, other: any) {
	if (one === other) {
		return true;
	}
	if (one === null || one === undefined || other === null || other === undefined) {
		return false;
	}
	if (typeof one !== typeof other) {
		return false;
	}
	if (typeof one !== 'object') {
		return false;
	}
	if ((Array.isArray(one)) !== (Array.isArray(other))) {
		return false;
	}

	let i: number;
	let key: string;

	if (Array.isArray(one)) {
		if (one.length !== other.length) {
			return false;
		}
		for (i = 0; i < one.length; i++) {
			if (!objectEquals(one[i], other[i])) {
				return false;
			}
		}
	} else {
		const oneKeys: string[] = [];

		for (key in one) {
			oneKeys.push(key);
		}
		oneKeys.sort();
		const otherKeys: string[] = [];
		for (key in other) {
			otherKeys.push(key);
		}
		otherKeys.sort();
		if (!objectEquals(oneKeys, otherKeys)) {
			return false;
		}
		for (i = 0; i < oneKeys.length; i++) {
			if (!objectEquals(one[oneKeys[i]], other[oneKeys[i]])) {
				return false;
			}
		}
	}

	return true;
}

interface Options<T> {
	callback: (value: T) => void;

	merge?: (input: T[]) => T;
	delay?: number;
}


export class DebounceTrigger<T> {

	private a = 0;
	protected b: T[] = [];
	private c: (value: T) => void;
	private d?: (input: T[]) => T;
	private readonly e: number;
	private f: any | undefined;

	constructor(options: Options<T>) {
		this.c = options.callback;
		this.d = options.merge;
		this.e = options.delay ?? 100;
	}

	private g(): void {
		this.a++;
	}

	private h(): void {
		if (this.a !== 0 && --this.a === 0) {
			if (this.d) {
				const items = Array.from(this.b);
				this.b = [];
				this.c(this.d(items));

			} else {
				while (!this.a && this.b.length !== 0) {
					this.c(this.b.shift()!);
				}
			}
		}
	}

	trigger(item: T): void {
		if (!this.f) {
			this.g();
			this.f = setTimeout(() => {
				this.f = undefined;
				this.h();
			}, this.e);
		}

		if (this.a !== 0) {
			this.b.push(item);
		} else {
			this.c(item);
		}
	}
}
